* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

[type='button'],
[type='button']:hover {
  -webkit-appearance: none;
}

.App-header {
  background-color: #282c34;
  min-height: 5rem;
  color: white;
  padding: 0 4rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.App-menu {
  height: 100%;
}

.App-menu-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin: 0;
}

.App-menu-nav-link {
  margin-left: 0.6rem;
  color: #007bff;
}

.App-menu-nav-link:hover {
  color: #0069d9;
  text-decoration: none;
  cursor: pointer;
}

.App-title {
  font-size: 2rem;
  font-weight: normal;
  margin-bottom: 0;
  height: 100%;
}

.Dictionary-list {
  width: 100%;
  list-style-type: none;
  margin-bottom: 1rem;
}

.Dictionary-list-item {
  padding: .75rem 1.25rem;
  margin-bottom: -1px;
  border: 1px solid rgba(0,0,0,.125);
  background-color: #fff;
  display: block;
  width: 100%;
}

.Dictionary-list-item:hover {
  cursor: pointer;
  background-color: #dee2e6;
  color: #212529;
}

.Dictionary-list-item[disabled] {
  cursor: default;
}

.Dictionary-list-item[disabled]:hover {
  background-color: #fff;
  color: default;
}

.Dictionary-view {
  margin-top: 1rem;
}

.Dictionary-view th:first-child {
  width: 40%;
}

.Dictionary-view th:nth-child(2) {
  width: 40%;
}

.Dictionary-view th:last-child {
  width: 20%;
}

.Dictionary-view-alert {
  margin-top: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.Dictionary-view-alert-button {
  cursor: pointer;
  font-weight: bold;
  margin-left: 1rem;
  font-size: 1.2rem;
}

.active {
  background-color: #6c757d;
  color: white;
}

.btn-toolbar .btn {
  margin-right: 0.3rem;
}

